import React from 'react';
import {Card} from 'react-bootstrap'



const cardStyle = {
    width:"100%",
    justifyContent:"center",
    boxShadow:"3px 4px 3px black"}
    const faStyleDice = {fontSize: "4em", color: "crimson"}
const SectionOne = () =>{
    return(
        <div className = "SectionOne">
            <Card style = {cardStyle}>
                <Card.Body>
                    <h2>The Experiment</h2>
                   
                    <p>In order to get a better understanding of the type of data this question 
                    can generate, our team created a program to record every aspect of the experiment. 
                    We created two random generators ranging between 1-6. With every click of the button 
                    our “dice” would display the number we rolled and their sum. The program must be keeping 
                    track of the amount of times that a sum was rolled. Here they refer to the sum as i .
                    It is important to keep track of the amount of times a specific value of i appears to 
                    calculate the probability of a 6 being rolled for that i  moving forward.</p>
                    <br/>
                    <p><i style={faStyleDice} class="fas fa-dice fa-lg"></i></p>
                    <br/>
                    <p>For all values of i <strong>&lt; 7</strong> the probability of a six being rolled will remain at 0 ,trivially.
                    For i ≥ 7, the way that the probability is calculated is that the program will keep tally how many times a specified 
                value of i appears. Because it takes two dice to produce the sum, each roll will add two possibilities for the six to appear. 
                Thus, our algorithm to calculate the probability that at least one of the dice lands on a six is to divide the amount of times a 
                6 appears on a roll for a specified i by the number of all times i has appeared multiplied by two (accounting for each of the dice.) </p>
                </Card.Body>
            </Card>
        </div>
    )
}

export default SectionOne