import React from 'react';
import {Card} from 'react-bootstrap'

const cardStyle = {
    width:"100%",
    justifyContent:"center",
    boxShadow:"3px 4px 3px black"}
    

    const faStyleDiceSix = {fontSize: "4em", color: "rgb(0, 168, 107)"}


const SectionTwo = () =>{
    return(
        <div className = "SectionTwo">
            <Card style = {cardStyle}>
                <Card.Body>
                    <h2>Theory</h2>
                    <p>It is known that the sum of rolling two fair dice 
                yields a finite set of possibilities. 
                The smallest sum two dice 
                can roll is a two and the largest being a 12. 
                This projected through an experiment will produce a variety of 
                outcomes versus the expected frequency of each sum. </p>
                <p>But taking it a step further, we are interested in the 
                frequency of a single value from either of the two dice. </p>
                <p><i style={faStyleDiceSix} class="fas fa-dice-six"></i></p>
                <p>
                    
                In particular, we are observing the amount of times a six was rolled 
                from either dice to contribute to the two dice sum 
                versus the total number of times that sum was achieved. </p>
                </Card.Body>
            </Card>
        </div>
    )
}

export default SectionTwo